.navbar{
    height: 60px;
}

.navbar, .nav-container{
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.nav-left{
    flex: 1;
}

.nav-left, .nav-logo{
    cursor: pointer;
}

.nav-center{
    flex: 1;
    display: flex;
    align-items: center;
}

.nav-search-container{
    display: flex;
    align-items: center;
    border: 1px solid lightgrey;
    margin-left: 25px;
    padding: 5px;
    color: lightgrey;
}

.nav-search-input{
    border: none;
}

.nav-right{
    width: 100%;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.menu-item{
    display: flex;
    align-items: center;
}
.menu-item a{
    margin: 0 25px;
}
