.slider-container{
    width: 100%;
    height: 100vh;
    display: flex;
    position: relative;
}

.sli-arrow-left, .sli-arrow-right{
    top: 0;
    bottom: 0;
    margin: auto;
    display: flex;
    position: absolute;
    background-color: white;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    cursor: pointer;
}

.sli-arrow-left{
    left: 10px;
}

.sli-arrow-right{
    right: 10px;
}

.slide-container{
    height: 100%;
}

.slide{
    display: flex;
    align-items: center;
    width: 100vw;
    height: 100vh;
}

.slide-img{
    height: 100%;
    flex: 1;
}

.slide-img img{
    height: 80%;
}

.slide-info{
    flex: 1;
}